body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-size:cover;font-family:Arial;text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.login .wrap{max-width:550px;max-height:600px;border-radius:20px;margin:auto;background:rgba(0,0,0,0.8);box-sizing:border-box;padding:40px;color:#fff;margin-top:50px}.login input[type=submit]{width:100%;box-sizing:border-box;padding:10px 0;margin-top:30px;outline:none;border:none;background:#60adde;opacity:0.7;border-radius:20px;font-size:20px;color:#fff}.login input[type=submit]:hover{background:#003366;opacity:0.7}.login h1{text-align:center}.login a{position:relative;display:inline-flex;padding:25px 30px;margin:40px 0;color:#03e9f4;font-size:24px;text-decoration:none;text-transform:uppercase;overflow:hidden;transition:0.5s;letter-spacing:4px}.login a:hover{background:#03e9f4;color:#050801;box-shadow:0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;text-decoration:none}.login a span{position:absolute;display:block}.login a span:nth-child(1){top:0;left:-100%;width:100%;height:2px;background:linear-gradient(90deg, transparent, #03e9f4);-webkit-animation:animate1 1s linear infinite;animation:animate1 1s linear infinite}@-webkit-keyframes animate1{0%{left:-100%}50%,100%{left:100%}}@keyframes animate1{0%{left:-100%}50%,100%{left:100%}}.login a span:nth-child(2){top:-100%;right:0;width:2px;height:100%;background:linear-gradient(180deg, transparent, #03e9f4);-webkit-animation:animate2 1s linear infinite;animation:animate2 1s linear infinite;-webkit-animation-delay:0.25s;animation-delay:0.25s}@-webkit-keyframes animate2{0%{top:-100%}50%,100%{top:100%}}@keyframes animate2{0%{top:-100%}50%,100%{top:100%}}.login a span:nth-child(3){bottom:0;right:-100%;width:100%;height:2px;background:linear-gradient(270deg, transparent, #03e9f4);-webkit-animation:animate3 1s linear infinite;animation:animate3 1s linear infinite;-webkit-animation-delay:0.5s;animation-delay:0.5s}@-webkit-keyframes animate3{0%{right:-100%}50%,100%{right:100%}}@keyframes animate3{0%{right:-100%}50%,100%{right:100%}}.login a span:nth-child(4){bottom:-100%;left:0;width:2px;height:100%;background:linear-gradient(360deg, transparent, #03e9f4);-webkit-animation:animate4 1s linear infinite;animation:animate4 1s linear infinite;-webkit-animation-delay:0.75s;animation-delay:0.75s}@-webkit-keyframes animate4{0%{bottom:-100%}50%,100%{bottom:100%}}@keyframes animate4{0%{bottom:-100%}50%,100%{bottom:100%}}

